* {
	margin: 0px;
	padding: 0px;
	border: none;
	outline: none;
	font-size: 100%;
}

body {
	font-size: 16px;
	color: #363636;
	line-height: 28px;
	font-weight: 400;
	background: #ffffff;
	-webkit-font-smoothing: antialiased;
	-moz-font-smoothing: antialiased;
	font-family: "Jost", sans-serif;
	font-style: normal;
}

a {
	text-decoration: none;
	cursor: pointer;
}

a:hover,
a:focus,
a:visited {
	text-decoration: none;
	outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	position: relative;
	font-weight: normal;
	margin: 0px;
	background: none;
	line-height: 1.2em;
}

section {
	position: relative;
}

textarea {
	overflow: hidden;
	resize: none;
}

button {
	background-color: transparent;
	outline: none !important;
	cursor: pointer;
	min-width: auto;
}

p,
.text {
	font-size: 15px;
	color: #696969;
	line-height: 24px;
	font-weight: 400;
	margin: 0;
}

::-webkit-input-placeholder {
	color: inherit;
}

::-moz-input-placeholder {
	color: inherit;
}

::-ms-input-placeholder {
	color: inherit;
}

.auto-container {
	position: static;
	max-width: 1310px;
	padding: 0px 15px;
	margin: 0 auto;
	width: 100%;
	z-index: 10;
}

.small-container {
	position: static;
	max-width: 900px;
	padding: 0px 15px;
	margin: 0 auto;
}

.page-wrapper {
