/*==========================
Owl Carousel Default Nav
===========================*/
.owl-carousel.no-arrows .owl-nav {
	display: none;
}

.owl-carousel.no-dots .owl-dots {
	display: none;
}

.default-nav .owl-nav {
	position: absolute;
	left: 0;
	top: 50%;
	width: 100%;
	margin-top: -25px;
	transition: all 500ms ease;
}

.default-nav .owl-next,
.default-nav .owl-prev {
	position: absolute;
	left: -80px;
	top: 0;
	height: 50px;
	width: 50px;
	color: #021f4a;
	background: #ffffff;
	font-size: 20px;
	text-align: center;
	line-height: 50px;
	border-radius: 50%;
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
	transition: all 500ms ease;
}

.default-nav .owl-next {
	left: auto;
	right: -80px;
}

.default-nav .owl-next:hover,
.default-nav .owl-prev:hover {
	color: #ffffff;
	background-color: #021f4a;
}

/*== Owl Dots ===*/
.default-nav .owl-dots {
	position: absolute;
	bottom: 0;
	width: 100%;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
}

.default-nav .owl-dot {
