.ls-section .filters-backdrop {
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	width: 1000%;
	background: #1b2032;
	z-index: 99;
	opacity: 0;
	visibility: hidden;
	transition: all 300ms ease;
}

.active-filters .ls-section .filters-backdrop {
	opacity: 0.8;
	visibility: visible;
}

.ls-section .filters-column.hide-left {
	position: fixed;
	left: 0;
	top: 0;
	height: 100vh !important;
	max-width: 320px;
	width: 100%;
	padding: 0 !important;
	background: #fff;
	z-index: 99;
	overflow-y: auto !important;
	transition: all 300ms ease;
	opacity: 0;
	visibility: hidden;
	transform: translateX(-100%);
}

.ls-section .filters-column.hide-left .inner-column {
	min-height: 100%;
}

